<template>
  <div class="credit">
    <scroll ref="scroll" class="credit-content">
      <div>
        <!--<m-header :title="this.title"></m-header>-->
        <div class="img-wrapper">
          <div class="img-content">
            <a href="javascript:;">
              <img src="http://y.gtimg.cn/music/photo_new/T003R720x288M000002EnE9V2QsHAb.jpg">
            </a>
          </div>
        </div>
        <div class="info-list">
          <ul>
            <li class="item">
              <div class="icon">
                <img src="./user.png" alt="">
              </div>
              <div class="text">
                <h2 class="name" v-html=""></h2>
                <p class="desc" v-html=""></p>
              </div>
              <router-link class="right" to="/basic">
                <p class="text"></p>
                <i class="right-icon"></i>
              </router-link>
            </li>
            <li class="item">
              <div class="icon">
                <img src="./phone.png" alt="">
              </div>
              <div class="text">
                <h2 class="name" v-html=""></h2>
                <p class="desc" v-html=""></p>
              </div>
              <router-link class="right" to="/phone_check">
                <p class="text"></p>
                <i class="right-icon"></i>
              </router-link>
            </li>
            <li class="item">
              <div class="icon">
                <img src="./alipay.png" alt="">
              </div>
              <div class="text">
                <h2 class="name" v-html=""></h2>
                <p class="desc" v-html=""></p>
              </div>
              <div class="right">
                <p class="text"></p>
                <i class="right-icon"></i>
              </div>
            </li>
            <li class="item">
              <div class="icon">
                <img src="./taobao.png" alt="">
              </div>
              <div class="text">
                <h2 class="name" v-html=""></h2>
                <p class="desc" v-html=""></p>
              </div>
              <div class="right">
                <p class="text"></p>
                <i class="right-icon"></i>
              </div>
            </li>
            <li class="item">
              <div class="icon">
                <img src="./mbri-users.png" alt="">
              </div>
              <div class="text">
                <h2 class="name" v-html=""></h2>
                <p class="desc" v-html=""></p>
              </div>
              <div class="right">
                <p class="text"></p>
                <i class="right-icon"></i>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </scroll>
  </div>
</template>

<script type="text/ecmascript-6">
  import Scroll from 'base/Scroll/Scroll'
  import MHeader from 'components/m-header/m-header'

  export default {
    data () {
      return {
        title: ''
      }
    },
    components: {
      MHeader,
      Scroll
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .credit
    position fixed
    width 100%
    top 0
    bottom 49px
    .credit-content
      height 100%
      overflow hidden
      .img-wrapper
        padding-top 40%
        position relative
        width 100%
        height 0
        overflow hidden
        .img-content
          position: absolute
          top: 0
          left: 0
          width: 100%
          height: 100%
          a
            display: block;
            width: 100%;
            overflow: hidden;
            text-decoration: none;
            img
              display: block;
              width: 100%;

      .info-list
        padding: 0 10px;
        .item
          display: flex;
          box-sizing: border-box;
          padding 10px 0
          align-items: center;
          border-bottom 1px solid #eee
          .icon
            flex: 0 0 45px;
            width 45px
            min-height 45px
            margin-right: 10px;
            img
              width 100%
          .text
            display: flex;
            flex-direction: column;
            justify-content: center;
            flex: 1;
            overflow: hidden;
            .name
              margin-bottom: 5px
              font-size $font-size-medium-x
              color: $color-text-d
            .desc
              color: $color-text-l
              font-size $font-size-small-s
          .right
            flex: 0 0 50px;
            display: flex;
            flex-direction: row;
            justify-content: center;
            overflow: hidden;
            .text
              font-size $font-size-medium
              color $color-text-d
            .right-icon
              display: block
              width 20px
              min-height 20px
              background url(right.png) center no-repeat;
              background-size 100% 100%
</style>



